import './styles.css';
import Header from '../../Components/Header';
import alt from '../../constants/altsImg';
import OpinionAboutUs from '../../assets/img/opinion-about-us.png';

function AboutUs() {
  return (
    <div>
      <Header />
      <div id="main-content-aboutus">
        <img src={OpinionAboutUs} id="image-option-aboutus" alt={alt.ABOUT_US} />
        <div id="text-aboutus">
          <p id="title-aboutus">Ol, ns somos a Voter!</p>
          <div>
            <p className='text-aboutus-middle'>Ns somos uma forma simples e democrtica de criar votaes e descobrir a opinio das pessoas.</p>
            <p className='text-aboutus-middle'>Nascemos com o sonho de criar um ambiente livre e justo na internet, permitindo que todas as pessoas possuam voz e sejam ouvidas.</p>
            <p className='text-aboutus-middle'> <b>fcil, seguro e rpido</b>. Com a Voter, voc cria votaes em segundos, permite que seu pblico vote em suas enquetes sem fazer cadastros longos e demorados e ainda veja o resultado das suas votaes ao vivo.</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AboutUs;
